<template>

<div class="wrapper">
<div class="wrapper-content">

<section>

<div class="container"> 

<h1>{{ title }}</h1>
  
<NewMessege v-if="NewMessege" :NewMessege="NewMessege"/> 

</div>

</section>


<div class="new-note">
<label>Title</label> 
<input type="text" v-model="note.title">
<label>Description</label> 
<textarea v-model="note.descr"></textarea>
      
<button class="btn btnPrimary" @click="AddNote">New note</button>

</div>

<notes :notes="notes" @remove="removeNote"/>


</div>

</div>



</template>

<script>

import NewMessege from '@/components/NewMessege.vue'
import notes from '@/components/ZametkiNovye.vue'

export default {
  name: 'App',
  components: {
    NewMessege,
    notes
  },
  data () {
    return {
    title: 'Notes App',
    NewMessege: null,
    note: {
      title:'',
      descr:''
    },
    notes: [
      {
        title: 'First note',
        descr: 'Description for first note',
        date: new Date(Date.now()).toLocaleString()
      },
      {
        title: 'Secons note',
        descr: 'Description for second note',
        date: new Date(Date.now()).toLocaleString()
      },
      {
        title: 'Third note',
        descr: 'Description for third note',
        date: new Date(Date.now()).toLocaleString()
      },
    ]
  }
  },
  methods: {
    AddNote () {
      let {title, descr} = this.note

      if (title === '') {

        this.NewMessege = 'Wrong note'
        return false

      }
      this.notes.push({
        title,
        descr,
        date: new Date(Date.now()).toLocaleString()
      })
      this.NewMessege = null
      this.note.title = ''
      this.note.descr = ''
    },
    removeNote (index) {
      this.notes.slice(index, 1)
    }
  }
}
</script>

<style>

.new-note {
  text-align: center;
}

</style>

